.strauto {
    width: 100%;
}

.strauto .wrapper {
    margin-top: 40px;
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 50px;
}

.strauto .slide img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: top;
}

.strauto .slide .slide_block {
    height: 300px;
    padding: 10px;
    border: none;
}

.strauto .slide .slide_block_two {
    padding: 10px;
}

.strauto .wrapper .wrapper_one {
    width: 100%;
    min-width: 0;
    overflow: hidden;
}

.strauto .wrapper .icons {
    margin-top: 5px;
    display: flex;
    flex-wrap: wrap;
}

.strauto .wrapper .icon_div {
    width: 50%;
    height: 20px;
    display: flex;
    align-items: center;
    margin-bottom: 20px;
}

.strauto .wrapper .icon_div p {
    margin-left: 10px;
}

.strauto .icon_c {
    color: var(--green);
}

.strauto .dsy_prise .boxx {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 15px;
    background: linear-gradient(90deg, #00970F 0%, #04C700 100%);
    padding: 15px 20px;
    margin: 20px 0 20px 0;
}

.strauto .boxx h4 {
    color: #fff;
    font-size: 16px;
    font-weight: 400;
}
.strauto .boxx h2 {
    color: #fff;
    font-size: 16px;
    font-weight: 500;
}
.strauto .dsy_prise .box {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 15px;
    background: #ffffff;
    padding: 15px 20px;
    box-shadow: -2px -2px 5px rgba(30,30,30,.14), 2px 2px 5px rgba(30,30,30,.14);
}

.strauto .box h4 {
    color: #000;
    font-size: 16px;
    font-weight: 400;
}
.strauto .box h2 {
    color: #000;
    font-size: 16px;
    font-weight: 500;
}


.strauto .wrapper .wrapper_one h1 {
    color: #000;
    font-size: 18px;
    font-weight: 600;
    line-height: 1;
    margin-top: 15px;
    margin-bottom: 10px;
}

.strauto .wrapper .wrapper_one p {
    color: #000;
    font-size: 14px !important;
    font-weight: 300 !important;
}

.strauto .wrapper .wrapper_two h1 {
    color: #1a1a1a;
    font-size: 22px;
    font-weight: 500;
    line-height: 1;
}

.strauto .wrapper .colored-star {
    color: yellow;
}
.strauto .datePicker{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 14px;
    gap: 8px;
    border: 1px solid  #14ae5c;
    background:  #fff;
    box-shadow: 0 1px 2px 0 rgba(16, 24, 40, 0.05);
    color: #0f0f0f;
    cursor: pointer;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    outline: none;

}

.react-datepicker__month-container{
    border: 1px solid #14ae5c;
    outline: none;
}
.react-datepicker_portal .react-datepickercurrent-month, .react-datepickerportal .react-datepicker-time_header{
    color: #fff;
}
.react-datepicker_header:not(.react-datepicker_header--has-time-select){
    background-color:#14ae5c;

}
.react-datepicker__input-container input {
    width: 100%;
    padding: 10px 14px;
    outline: none;
    border: 1px solid var(--green);
    box-shadow: 0 1px 2px 0 rgba(16, 24, 40, 0.05);
    color:#0f0f0f;
    font-size: 16px;
    font-weight: 400;
    margin-top: 10px;
}

.strauto .datePickers{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 14px;
    gap: 8px;
    border: 1px solid #14ae5c;
    background:  #fff;
    box-shadow: 0 1px 2px 0 rgba(16, 24, 40, 0.05);
    color: #0f0f0f;
    cursor: pointer;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    outline: none;

}
.strauto .data_pic{
    position: relative;
}
.strauto .BsCalendarDate{
    position: absolute;
    top: 20px;
    right: 10px;
    cursor: pointer;
    z-index: 9999;
    color: #14ae5c;

}
.strauto .tour_recuest {
    margin-top: 20px;
    width: 100%;
    min-height: 200px;
    padding: 25px;
    background: #fff;
    box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.1);
}

.strauto .tour_recuest h1 {
    color: var(--green);
    font-size: 30px;
    font-weight: 700;
}

.strauto .tour_recuest p {
    color: #000;
    font-size: 14px;
    font-weight: 300;
    line-height: 1;
}

.strauto .tour_recuest h3 {
    margin-top: 25px;
    color: var(--green);
    font-size: 16px;
    font-weight: 600;
}

.strauto .text_rev {
    width: 100% !important;
}

.strauto .tour_recuest .data {
    margin-top: 15px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 15px;
    row-gap: 15px;
}

.strauto .tour_recuest textarea,
.strauto .tour_recuest .data input {
    width: 100%;
    padding: 10px 14px;
    outline: none;
    border: 1px solid var(--green);
    box-shadow: 0 1px 2px 0 rgba(16, 24, 40, 0.05);
    color:#0f0f0f;
    font-size: 16px;
    font-weight: 400;
}

.strauto .tour_recuest textarea {
    height: 110px;
    margin-top: 2px;
}

.strauto .tour_recuest .send {
    margin-top: 15px;
    padding: 10px 70px;
    outline: none;
    border: none;
    cursor: pointer;
    color:  #fcfcfc;
    font-size: 18px;
    font-weight: 600;
    background: var(--green);
}

.strauto .input_box {
    position: relative;
}

.strauto .icon {
    position: absolute;
    top: 10px;
    right: 10px;
    color: var(--green);
    cursor: pointer;
}

.strauto .select_input {
    top: 0;
    position: absolute;
    background: #00000000;
    width: 100%;
    height: 42px;
    cursor: pointer;
}

@media (max-width: 1100px) {
    .strauto .wrapper {
        display: grid;
        grid-template-columns: 1fr;
    }
}

@media (max-width: 600px) {
    .strauto .tour_recuest .data {
        display: grid;
        grid-template-columns: 1fr;
    }
    .strauto .disabled-date {
        background-color: red !important;
        color: white !important;
    }
    .rdrDateRangePickerWrapper {
        display: block !important;
        -webkit-user-select: none;
        -moz-user-select: none;
        user-select: none;
    }
    .strauto .wrapper .wrapper_two h1 {
        font-size: 20px;
    }
}

@media (max-width: 500px) {
    .strauto .tour_recuest .send {
        width: 100%;
    }
    .strauto .tour_recuest {
     
        padding: 0;
       
    }
}
