#list {
    margin-top: 50px;
    width: 100%;
}

#list .tour_box {
    width: 100%;
    min-height: 220px;
    display: flex;
    align-items: center;
    display: grid;
    grid-template-columns: 1fr 2.5fr;
    gap: 30px;
    padding-right: 30px;
    margin-bottom: 30px;
    background: #fff;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.15);
}
#list .tour_box img {
    width: 100%;
    height: 284px;
    object-fit: cover;
    object-position: center;
}
#list .content_container {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
}
#list .August {
    display: flex;
    font-size: 18px;
    font-weight: 500;
    margin-top: 10px;
}
#list .content_box h1 {
    color: #1a1a1a;
    font-size: 22px;
    font-weight: 700;
}
#list .colored-star {
    color: yellow;
}
#list .content_box p {
    color: #4f4f4f;
    font-size: 14px;
    font-weight: 400;
    margin: 10px 0 10px 0;
}
#list .content_box .btn {
    outline: none;
    border: none;
    margin-bottom: 37px;
    cursor: pointer;
    padding: 10px 20px;
    color:  #fcfcfc;
    font-size: 16px;
    font-weight: 500;
    background: #14ae5c;
}
#list .content_box {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 70%;
}
#list .content_box_two p {
    color: #0f0f0f;
    text-align: right;
    font-size: 15px;
    font-weight: 400;
    margin: 0;
}
#list .content_box_two .price_div {
    margin-left: auto;
    width: 100px;
    padding: 5px 15px;
    margin-top: 10px;
    border: 1px solid var(--green);
    display: flex;
    align-items: center;
    justify-content: center;
    color:  #0f0f0f;
    font-size: 17px;
    font-weight: 500;
}

#list .paginatio {
    display: flex;
    justify-content: center;
    margin-top: 20px;
}

#list .paginatio button {
    margin: 0 0;
    padding: 5px 5px;
    cursor: pointer;
}

#list .paginatio button {
    border: none;
    background: transparent;
}

#list .paginatio .arrow_left {
    font-size: 25px;
}
#list .paginatio .arrow_right {
    font-size: 25px;
}

@media (max-width: 950px) {
    #list .tour_box img{
        width: 100%;
    }
    #list .tour_box {
        display: flex;
        flex-direction: column;

        align-items: center;
        gap: 30px;
    }

    #list .content_container {
        display: flex;

        align-items: center;
        gap: 10px;
        margin-left: 20px;
    }
    .tour_box p {
        margin: 0;
    }
}

@media (max-width: 850px) {
    #list .tour_box {
        display: inline-flex;
        flex-direction: column;
        column-gap: normal;
        align-items: center;
    }
    #list .content_box_two p {
        text-align: start;
        flex-direction: column;
    }
    #list .content_container {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: 10px;
        margin-left: 20px;
    }
    .tour_box p {
        margin: 0;
    }
    #list .btn {
        display: none;
    }
}

@media (max-width: 450px) {
    #list .tour_box {
        display: flex;
        align-items: flex-start;
        margin-left: 20px;
    }
    #list .content_box_two p {
        text-align: start;
        margin-top: 10px;
    }
    #list .content_box .btn {
        margin-top: 10px;
    }
    #list .text_title {
        font-size: 20px;
    }
    #list .text_lorem {
        font-size: 14px;
    }
}
