.flex_all_from{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(310px, 1fr));
    gap: 20px;
    grid-auto-flow: dense;
}

.lead .flex {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.lead .all_big{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(310px, 1fr));
    gap: 20px;
    grid-auto-flow: dense;
    margin-top: 20px;
}
.lead .bigs{
    color: black;
    font-size: 18px;
    font-weight: 700;
}
.lead .flex_one {
    display: flex;
    align-items: center;
}

.lead .traveler {
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #ffffff;
    font-size: 25px;
    font-weight: 500;
    background: #14ae5c;
}

.lead .flex_one button {
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #ffffff;
    font-size: 25px;
    font-weight: 500;
    background: #14ae5c;
    border: none;
}


.lead .flex_one h1 {
    margin: 0 20px 0 20px;
    color: #14ae5c;
}

.lead form input {
    border: 1.5px solid #ddd ;
    outline: none;
    width: 100%;
    height: 40px;
    padding: 0 0 0 10px;
    background: transparent;
    color: black;
    font-size: 15px;
}

.lead form label {
    font-size: 14px;
    line-height: 22px;
    font-weight: 700;
    color: black;
    margin: 0 0 5px 0;
    appearance: none;
}

.lead .floor {
    display: flex;
    align-items: center;
}

.lead .floor h3 {
    font-size: 16px;
    line-height: 22px;
    font-weight: 700;
    color: black;
    appearance: none;
}

.lead .floor h2 {
    font-size: 14px;
    line-height: 22px;
    font-weight: 700;
    color: black;
    appearance: none;
}

.lead .floor input {
    width: 20px;
    height: 30px;
    margin: 0 0 0 5px;
}

.lead form .button {
    width: 100%;
    height: 40px;
    background: #14ae5c;
    border: 1.5px solid #14ae5c ;
    font-size: 18px;
    line-height: 22px;
    font-weight: 700;
    color: #ffffff;
    transition: 0.5s;
    margin: 20px 0 0 0;
}
.lead form .button:hover{
  background: #04C700;
}
.floor button{
    border: 1.5px solid #ddd ;
    background: transparent;
    width: 100px !important;
    height: 30px !important;
}
.floor .active_active{
    background: #14ae5c;
    color: #FFFFFF;
    border: none;
}



.lead .travel_out{
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: #333;
    background-color: #f5f5f5;
    border-color: #ddd;
    padding: 10px 15px;
    margin: 20px 0 0 0;
    cursor: pointer;
}

.lead .md_icons{
    font-size: 27px;
}

