.home_create {
    margin: 60px auto;
}

.home_create .img_home{
    position: absolute;
    min-height: 200px;
    max-width: 1540px;
    margin: -130px auto 0 103%;
    z-index: 1;
}
.home_create .tour_all{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(310px, 1fr));
    grid-gap: 32px;
    grid-auto-flow: dense;
}
.home_create .tour_all .tour_box {
    cursor: pointer;
    width: 100%;
    height: 240px;
    margin-bottom: 20px;
    position: relative;
    overflow: hidden;
}
.home_create .tour_all .tour_image img {
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    position: absolute;
    object-fit: cover;
    object-position: center;
}
.home_create .tour_all .tour_image {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 1;
}
.home_create .tour_all .content {
    bottom: 0;
    width: 100%;
    position: absolute;
    padding: 13px;
    width: 100%;
    display: grid;
    grid-template-columns: 2fr 1fr;
    z-index: 10;
}

.home_create .tour_all .content_block {
    width: 100%;
    display: flex;
    background-color: rgba(0,0,0, .3);
    grid-template-columns: 2fr 1fr;
    align-items: center;
    position: absolute;
    bottom: 0;
    padding: 10px;
    z-index: 15;
}

.content_block .content_box {
    width: 100%;
}

.home_create .tour_all .content h3 {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 10px;
}

.home_create .tour_all .content h3 b {
    color: #fff;
    font-size: 16px;
    font-weight: 500;
    line-height: 1;
}

.home_create .tour_all .content h3 span {
    color: #fff;
    font-size: 24px !important;
    font-weight: 600 !important;
    margin-left: 15px !important;
}

.home_create .tour_all .content p {
    color: #fff;
    font-size: 13px;
    font-weight: 300;
}

.home_create .tour_all .content h2 {
    margin-top: 5px;
    color: #fff;
    font-size: 14px;
    font-weight: 300;
    display: flex;
    align-items: center;
}

.home_create .content .box_h1 {
    color: #fff;
    font-size: 24px !important;
    font-weight: 600 !important;
    margin-left: 15px !important;
}

.home_create button{
    padding: 10px 30px;
    color: #FFFFFF;
    font-size: 16px;
    font-weight: 400;
    border: none;
    background-color: #14ae5c;
    display: flex;
    align-items: center;
    justify-content: center;
}
