.gallery {
    margin: 0 auto;
}
.gallery h1{
    color: #04C700;
    margin: 60px 0 10px 0;
}
.gallery .img_home {
    position: absolute;
    min-height: 200px;
    max-width: 1540px;
    margin: -130px auto 0 103%;
    z-index: 1;
}

.gallery .tour_all {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(310px, 1fr));
    grid-gap: 32px;
    grid-auto-flow: dense;
}

.gallery .tour_all .tour_box {
    cursor: pointer;
    width: 100%;
    height: 240px;
    position: relative;
    overflow: hidden;
}

.gallery .tour_all .tour_image img {
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    position: absolute;
    object-fit: cover;
    object-position: center;
}

.gallery .tour_all .tour_image {
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 1;
}

.gallery .tour_all .content {
    bottom: 0;
    width: 100%;
    position: absolute;
    padding: 13px;
    width: 100%;
    display: grid;
    grid-template-columns: 2fr 1fr;
    z-index: 10;
}

.gallery .tour_all .content_block {
    width: 100%;
    display: flex;
    background-color: rgba(0, 0, 0, .3);
    grid-template-columns: 2fr 1fr;
    align-items: center;
    position: absolute;
    bottom: 0;
    padding: 10px;
    z-index: 15;
}

.content_block .content_box {
    width: 100%;
}

.gallery .tour_all .content h3 {
    width: 100%;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 10px;
}

.gallery .tour_all .content h3 {
    color: #fff;
    font-size: 16px;
    font-weight: 500;
    line-height: 1;
}



.gallery .tour_all .content p {
    color: #fff;
    font-size: 13px;
    font-weight: 300;
}

.gallery .tour_all .content h2 {
    margin-top: 5px;
    color: #fff;
    font-size: 14px;
    font-weight: 300;
    display: flex;
    align-items: center;
}

.gallery .content .box_h1 {
    color: #fff;
    font-size: 24px !important;
    font-weight: 600 !important;
    margin-left: 15px !important;
}



@media (max-width: 550px) {
    .gallery .nome_block {
        display: none;
    }
    .gallery .home_none {
        display: block;
    }
    .gallery .div{
        display: flex;
        width: 100%;
        justify-content: space-between;
        align-items: center;
    }
}

@media (max-width: 428px) {
    .gallery .tour_all .tour_box {
        width: 100%;
        height: 140px;
        margin: 0 auto;
    }
}