#header {
    width: 100%;
}

#header .slider img {
    width: 100%;
    height: 346px;
}

#header .nomads {
    display: none;
    position: relative;
    z-index: 9999;
}


#header .life_felx {
    background-color: #fcfcfc;
    border-bottom: 1px solid #ddd;
    display: flex;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 10000;
    height: 62px;
    backdrop-filter: blur(3px);
    margin-bottom: 20px;
}

#header .life_felx .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

#header .flex_life ul {
    display: flex;
    align-items: center;
}

#header .flex_life ul li {
    display: flex;
    align-items: center;
    list-style: none;
    position: relative;
    color: #14ae5c;
    font-size: 12px;
    font-weight: 400;
    padding: 10px;
    line-height: 1;
}

#header .flex_life ul li:before {
    border-right: 1px solid #ddd;
    bottom: 0;
    content: "";
    position: absolute;
    right: 0;
    top: 0;
    margin: auto;
    height: 100%;
}

#header .flex_life ul li .linee {
    border-right: 1px solid #ddd;
}

#header .flex_life ul li:hover {
    color: #0085ba;
    font-size: 12px;
}

#header .flex_life ul li a {
    border-left: 1px solid #ffffff;
    color: #000;
    font-size: 12px;
    font-weight: 300;
}

#header .flex_life ul li a:hover {
    color: #0085ba;
    font-size: 12px;
}

.goog-te-gadget {
    font-size: 0 !important;
    color: #666;
    white-space: nowrap;
}

#header .nomads h3 {
    color: #000;
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    margin: 0 0 0 0;
}

.VIpgJd-ZVi9od-l4eHX-hSRGPd,
.VIpgJd-ZVi9od-l4eHX-hSRGPd:link,
.VIpgJd-ZVi9od-l4eHX-hSRGPd:visited,
.VIpgJd-ZVi9od-l4eHX-hSRGPd:hover,
.VIpgJd-ZVi9od-l4eHX-hSRGPd:active {
    display: none;
}

.VIpgJd-ZVi9od-ORHb {
    display: none;
    background: transparent;
}

.VIpgJd-ZVi9od-ORHb-OEVmcd {
    display: none;
}

.goog-te-gadget .goog-te-combo {
    width: 200px;
    display: flex;
    padding: 6px;
    background: #14ae5c;
    border: 1px solid #14ae5c;
    color: #ffffff;
    font-size: 12px;
    cursor: pointer;
    transition: 0.5s;
}

.goog-te-gadget .goog-te-combo:hover {
    background: #31642b;
    border: 1px solid #44e830;
}

#header .burger {
    display: none;
}

#header .slider {
    position: relative;
    margin-top: 0;
}

#header .slider img {
    width: 100%;
    height: 350px;
    position: relative;
    object-fit: cover;
    object-position: center;
    margin: 62px 0 0 0;
}

#header .slider_box {
    width: 390px;
    min-height: 212px;
    position: absolute;
    top: 211px;
    left: 40px;
}

#header .slider_box h1 {
    color: #fff;
    font-size: 22px;
    font-weight: 500;
    max-width: 300px;
}

#header .slider_box p {
    color: rgba(255, 255, 255, 1);
    font-size: 14px;
    font-weight: 300;
    line-height: 1.1;
    margin: 10px 0;
    max-width: 400px;
}

#header .slider_box button {
    margin-top: 0;
    padding: 8px;
    background: #14ae5c;
    color: #fcfcfc;
    font-size: 14px;
    font-weight: 500;
    display: flex;
    border: none;
    outline: none;
    cursor: pointer;
}

#header .input_date {
    border: none;
    outline: none;
}

#header .block {
    position: relative;
    margin-top: 20px;
    z-index: 9999;
    width: 100%;
    min-height: 52px;
    background: linear-gradient(90deg, #00970f 0%, #04c700 100%);
    backdrop-filter: blur(25px);
    align-items: center;
    padding: 13px 20px 10px 20px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}

#header .block_flex_one {
    display: flex;
}

#header .slick-dots {
    position: absolute;
    bottom: 10px;
    display: block;
    width: 100%;
    padding: 0;
    margin: 0;
    list-style: none;
    text-align: center;
}

#header .slick-dots li button:before {
    font-size: 10px;
    top: 0;
    left: 0;
    width: 20px;
    height: 20px;
    content: "";
    text-align: center;
    color: black;
}

.relative_one {
    position: relative !important;
}

#header .page_one {
    cursor: pointer;
}

#header .page_one {
    color: #ffffff;
    font-size: 16px;
    font-weight: 400;
    position: relative;
    cursor: pointer;
}

#header .select_header {
    position: absolute;
    top: 100%;
    background: #fff;
    margin-left: -18px;
    margin-top: 14px;
    border: 1px solid #bdbdbd;
    min-width: 160px;
    box-shadow: 0 0 2px rgba(0, 0, 0, 0.3);
    width: 200px;
    min-height: 45px;
    transition: opacity 0.3s ease;
    z-index: 9999 !important;
    padding: 0 20px 0 20px;
    display: none;
}

#header .relative_one {
    position: relative;
    margin: 0 20px;
}

#header .relative_one::before {
    content: "";
    top: 0;
    right: -38px;
    position: absolute;
    border-right: 1px solid #fff;
    display: block;
    padding: 24px 5px 0 10px;
    margin-right: 15px;
}

#header .relative_one::after {
    content: "";
    top: 0;
    right: -38px;
    position: absolute;
    display: block;
    padding: 24px 5px 0 10px;
    margin-right: 15px;
    background: transparent;
    width: 111px;
    height: 100%;
    z-index: -1;
}

#header .relative_one:hover .select_header,
#header .relative_one:hover::before,
.select_header:hover {
    display: block;
}

#header .hover {
    position: relative;
}

#header .hover::before {
    content: "";
    top: 0;
    right: -34px;
    position: absolute;
    border-right: 1px solid #fff;
    display: block;
    padding: 24px 5px 0 10px;
    margin-right: 15px;
}

#header .select-list {
    list-style-type: none;
    padding: 0;
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 0;
    z-index: 9999;
    margin-left: -12px;
}

#header .select-list li {
    padding: 8px;
    position: relative;
    color: #000;
}

#header .select-list li:hover {
    display: block;
    text-decoration: underline;
    width: 100%;
    color: #04c700;
    cursor: pointer;
}

#header .select-list li a {
    position: relative;
    font-size: 16px;
    font-weight: 400;
    color: #050505;
    display: block;
}

#header .select-list li a:hover {
    display: block;
    text-decoration: underline;
    width: 100%;
    color: #04c700;
}

#header .page {
    color: #ffffff;
    font-size: 16px;
    font-weight: 400;
}

#header .nav_block {
    display: none;
}

#header #toggle,
#header .hamburger,
#header .nav,
#header .nav_wrapper {
    display: none;
}

#header .hamburger {
    width: 3rem;
    height: 45px;
    z-index: 5;
    cursor: pointer;
}

#header .hamburger div {
    position: relative;
    width: 2.8em;
    height: 5px;
    border-radius: 3px;
    margin-top: 5px;
    background: #14ae5c;
    transition: all 0.3s ease-in-out;
}

#header .input_box {
    position: relative;
    display: none;
}

#header .icon {
    position: absolute;
    top: 6px;
    right: 10px;
    cursor: pointer;
}

#header .category_box {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 5px 14px;
    gap: 8px;
    border: 1px solid #FFF;
    background: #14ae5c;
    box-shadow: 0 1px 2px 0 rgba(16, 24, 40, 0.05);
    color: #FFF;
    cursor: pointer;
    font-size: 14px;
    font-weight: 300;
    line-height: 24px;
    outline: none;
}

.input_box input::placeholder {
    color: #FFF;
    font-weight: 400;
}

#header #toggle:checked + .hamburger .top-bun {
    transform: rotate(-45deg);
    margin-top: 13px;
}

#header #toggle:checked + .hamburger .bottom-bun {
    opacity: 0;
    transform: rotate(45deg);
}

#header #toggle:checked + .hamburger .meat {
    transform: rotate(45deg);
    margin-top: -5px;
}

#header .nav {
    margin-top: -110px;
    width: 100%;
    height: 120vh;
    border-radius: 20px;
    background: #fff;
    box-shadow: 0 0 15px 0 rgba(0, 0, 0, 0.1);
    transition: all 0.3s ease-in-out;
    padding-top: 140px;
    z-index: 1;
}

#header .nav_wrapper {
    margin: 0 auto;
    width: 90%;
    display: flex;
    align-items: start;
    flex-direction: column;
    animation: fixed_modal_menu 0.7s ease;
}

.sliders {
    position: relative;
}

.sliders:after {
    content: "";
    width: 20%;
    height: 140px;
    background: url("../../img/oop.png") center center no-repeat;
    background-size: 100%;
    position: absolute;
    top: 70px;
    right: 18px;
    z-index: 111;
}

@keyframes fixed_modal_menu {
    0% {
        opacity: 0;
        right: -200px;
    }
    100% {
        opacity: 1;
        right: 0;
    }
}

#header .nav_wrapper .login {
    margin: 10px;
}

#header .nav_wrapper .btn {
    width: 100%;
    margin-top: 25px;
}

#header .nav_wrapper .page {
    margin: 10px;
    transition: 0.3s ease;
}

#header .login,
#header .page {
    color: #ffffff;
}

#header .slider_one {
    display: none;
}

#header .language {
    color: #fff;
    background-color: #04c700;
    border: none;
    outline: none;
}

#header .i18n_all button {
    display: flex;
    justify-content: space-between;
    color: #fff;
    width: 100%;
    font-weight: 400;
    font-size: 14px;
    background: #14ae5c;
    border: 1px solid #fff;
    border-radius: 3px;
    padding: 5px 10px;
}

@media (max-width: 800px) {
    .goog-te-gadget .goog-te-combo {
        display: block;
    }

    #header .flex_life {
        display: none;
    }

    #header .block {
        display: none;
    }

    #header .burger {
        display: block;
        cursor: pointer;
        position: relative;
        z-index: 9999;
    }

    #header .celina {
        display: none;
    }

    #header .nav_block {
        display: block;
    }

    .input_box::placeholder {
        color: #000;
    }

    #header .nav_block {
        left: 0;
        position: absolute;
        width: 100%;
        background: #eaf5ef;
        -webkit-backdrop-filter: blur(25px);
        backdrop-filter: blur(25px);
        padding: 20px;
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        overflow: hidden;
        z-index: 999;
        margin-top: -10px;
    }

    #header .nav_block .page {
        color: #000;
        text-decoration: none;
        text-transform: uppercase;
        display: block;
        padding: 19px 0;
        border-bottom: 1px solid #ffffff;
        margin-left: -1px;
    }

    #header .page_one {
        color: #000;
        text-decoration: none;
        text-transform: uppercase;
        display: block;
        padding: 19px 0;
        border-bottom: 1px solid #ffffff;
        margin-left: -1px;
    }


    #header .hamburger,
    #header .nav {
        display: block;
        padding: 1px 0 0 0;
        margin-left: auto;
        margin-top: 6px;
    }

    #header .page {
        color: #000;
        font-size: 16px;
        font-weight: 400;
    }

    #header .ul_li_a {
        color: #000 !important;
        text-decoration: none;
        text-transform: uppercase;
        display: block;
        padding: 19px 0;
        border-bottom: 1px solid #ffffff;
        margin: 0 0 10px 20px;
    }

    #header .nomads {
        display: block;
        position: relative;
        z-index: 9999;
    }

    #header .kyrgyzstan {
        display: flex;
        align-items: center;
        justify-content: space-between;
        cursor: pointer;
    }
}

@media (max-width: 730px) {
    #header .nomads {
        display: block;
        position: relative;
        z-index: 9999;
    }

    #header .i18n {
        display: none;
    }

    #header .input_box {
        display: block;
    }

    #header .flex_life {
        display: none;
    }

    #header .flex_life ul {
        display: none;
    }

    #header .life_felx .container {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
}

@media (max-width: 500px) {
    #header .slider_box {
        width: 100%;
        min-height: 212px;
        position: absolute;
        top: 122px;
        left: 10px;
        display: flex;
        align-items: baseline;
        justify-content: left;
        flex-direction: column;
    }

    #header .flex_life {
        display: none;
    }

    #header .flex_life ul {
        display: none;
    }


    #header .nomads {
        display: block;
        position: relative;
        z-index: 9999;
    }

    #header .sliders::after {
        width: 50%;
        right: 0;
        top: -32px;
    }

    #header .sliders {
        display: none;
    }

    #header .slider_one {
        display: block;
        margin-top: 60px;
    }

    #header .slider_one:after {
        content: "";
        width: 40%;
        height: 140px;
        background: url("../../img/oop.png") center center no-repeat;
        background-size: 100%;
        position: absolute;
        top: 55px;
        right: 18px;
        z-index: 111;
    }

    #header .slider img {
        width: 100%;
        height: 250px;
        position: relative;
        object-fit: cover;
        object-position: center;
        margin: 0 0 0 0;
    }
}

.slick-next {
    right: -25px;
    display: none !important;
}

.slick-prev,
.slick-next {
    display: none !important;
}

#header .i18n button {
    color: #fff;
    font-weight: 400;
    font-size: 14px;
    background: #14ae5c;
    border: none;
    padding: 5px 10px;
    margin: 5px 5px 5px;
}
