@font-face {
    font-family: Gilroy;
    src: url("./fonts/Roboto-Thin.ttf");
    font-weight: 100;
}

@font-face {
    font-family: Gilroy;
    src: url("./fonts/Roboto-Light.ttf");
    font-weight: 300;
}

@font-face {
    font-family: Gilroy;
    src: url("./fonts/Roboto-Regular.ttf");
    font-weight: 400;
}

@font-face {
    font-family: Gilroy;
    src: url("./fonts/Roboto-Medium.ttf");
    font-weight: 500;
}

@font-face {
    font-family: Gilroy;
    src: url("./fonts/Roboto-Bold.ttf");
    font-weight: 700;
}

@font-face {
    font-family: Gilroy;
    src: url("./fonts/Roboto-Black.ttf");
    font-weight: 900;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: Gilroy, sans-serif;
}

body {
    top: 0 !important;
}

:root {
    --green: #14ae5c;
}

.container {
    margin: 0 auto;
    max-width: 1140px;
    width: 90%;
}

li {
    list-style: none;
}

a {
    text-decoration: none;
}

input {
    outline: none;
}

input:disabled {
    background: transparent;
}

button {
    outline: none;
    cursor: pointer;
}

button:hover {
    opacity: 0.8;
}

.loading_div {
    width: 100%;
    height: 500px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.app_all {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    right: 0;
    top: 190px;
    position: fixed;
    z-index: 10000;
}
.instagram {
    display: block;
    margin-right: 20px;
    background: url("./img/instagram.431fa61d.svg") no-repeat center;
    background-size: 100%;
    height: 42px;
    width: 42px;
}
.telegram {
    display: block;
    margin-top: 15px;
    margin-right: 20px;
    background: url("./img/telegram.76bc1282.svg") no-repeat center;
    background-size: 100%;
    height: 42px;
    width: 42px;
}
.tik-tok {
    border-radius: 50%;
    display: block;
    margin-top: 15px;
    margin-right: 20px;
    background: url("./img/tiktok.png") no-repeat center;
    background-size: 100%;
    height: 42px;
    width: 42px;
}
.whatsapp {
    display: block;
    margin-top: 15px;
    margin-right: 20px;
    background: url("./img/whatsapp.a93c89b4.svg") no-repeat center;
    background-size: 100%;
    height: 42px;
    width: 42px;
}
:focus, :active {
    outline: none;
}

a, a:active, a:focus {
    outline: 0;
    -webkit-tap-highlight-color: transparent;
}

h1 {
    color: #000;
    font-size: 26px;
    font-weight: 500;
    line-height: 40px;
}

@media (max-width: 450px) {
    .instagram {
        height: 38px;
        width: 38px;
    }
    .telegram {
        height: 38px;
        width: 38px;
    }
    .whatsapp {
        height: 38px;
        width: 38px;
    }
    .app_all {
        margin-right: -10px;
        top: 250px;
    }
}
* {
    scrollbar-color: green orange; 
    scrollbar-width: thin; 
  }
  
  *::-webkit-scrollbar {
    width: 10px; 
  }
  
  *::-webkit-scrollbar-thumb {
    background-color: #14ae5c; 
  }

